import React, {Component} from 'react';
import PropTypes from 'prop-types';

class IconPrint extends Component {
    render() {
        return (
            <svg width={this.props.size} height={this.props.size} viewBox="0 0 16 16" className="bi bi-printer-fill" fill="currentColor"
                 xmlns="http://www.w3.org/2000/svg">
                <path d="M5 1a2 2 0 0 0-2 2v1h10V3a2 2 0 0 0-2-2H5z"/>
                <path fillRule="evenodd" d="M11 9H5a1 1 0 0 0-1 1v3a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1v-3a1 1 0 0 0-1-1z"/>
                <path fillRule="evenodd"
                      d="M0 7a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v3a2 2 0 0 1-2 2h-1v-2a2 2 0 0 0-2-2H5a2 2 0 0 0-2 2v2H2a2 2 0 0 1-2-2V7zm2.5 1a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z"/>
            </svg>
        );
    }
}
IconPrint.defaultProps = {
    size: "1rem"
}

IconPrint.propTypes = {
    size: PropTypes.string.isRequired,
};

export default IconPrint;
